export var createViewItem = {
    nav: function (type, page) { return ({
        type: type,
        page: page,
    }); },
    page: function (page, active) { return ({
        type: 'page',
        page: page,
        active: active,
    }); },
    ellipsis: function (position) { return ({
        type: 'ellipsis',
        position: position,
    }); },
};
